import { useNavigate } from 'react-router-dom';
import { ICompanyDetail } from '../../../interfaces';

interface Props {
  className?: string;
  companyDetail: ICompanyDetail;
  hSize: string;
  wSize: string;
}

export default function CompanyThumbCard(props: Props) {
  const { className, companyDetail, hSize, wSize } = props;
  const navigate = useNavigate();
  return (
    <button
      type="button"
      className={`flex-shrink-0 border border-white/20 bg-white/20 hover:bg-white/50 ${
        className || ''
      }`}
      onClick={() => navigate(`/booth/${companyDetail.id}`)}
      style={{ width: wSize, height: hSize }}
    >
      <img
        src={companyDetail.image}
        className="block h-full w-full object-cover opacity-0"
        alt={companyDetail.name}
      />
    </button>
  );
}
